.auth-inner {
  width: 450px;
  text-align: center;
  margin: auto;
  background: #ffffff;
  box-shadow: 0px 14px 80px rgba(34, 35, 58, 0.2);
  padding: 40px 55px 45px 55px;
  border-radius: 30px;
  transition: all .3s;
}

.auth-wrapper {
    width: 65rem;
    margin: 0 auto;
    text-align: center;
    box-shadow: 0px 14px 80px rgba(34, 35, 58, 0.2);
}
